.home-background {
  height: 89vh;
  background-image: url("../../assets/demo.svg");
  background-position-x: center;
  background-position-y: 35%;
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-size: cover;
  background-origin: padding-box;
  background-clip: border-box;
}
.home-container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 75vh;
  text-align: center;
}
.home-heading-container {
  max-width: 800px;
  display: flex;
  flex-direction: column;
  margin-top: 5%;
  align-items: center;
}
.home-heading {
  font-size: 3rem;
  font-family: "Montserrat", sans-serif;
  text-align: center;
}
.home-description {
  font-size: 1rem;
  text-align: center;
}

.home-container h1 {
  margin-bottom: 0;
}

.blue {
  font-weight: bold;
  color: #2026d2;
  font-size: 1.5rem;
  position: relative;
  width: 70%;
}
