@media (max-width: 450px) {
  .home-background {
    background-image: none;
  }
  .home-container {
    height: 85%;
  }
  .home {
    background: none;
    background-color: #f5f5ff;
  }
  .home-heading {
    font-size: 2rem;
  }
  .blue {
    font-size: 1rem;
    width: 90%;
  }
  .home-heading-container > a:nth-child(3) {
    padding-top: 12%;
  }
}
